.LogedInHeader {
    width: 100%;
    margin-top: 64px;
}

.Toolbar {
    background: #58595B;
    display: flex;
    align-items: center;
    color: #fff;
    border-bottom: 1px solid #ccc;
}

.Grow {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.Grow div:first-child {
    display: flex;
    align-items: center;
}

.Grow div:first-child span:first-child {
    margin-right: 10px;
}

.Grow div:nth-child(2) {
    display: none;
}

.rightSection {
    display: flex;
    align-items: center;
}

.rightSection div:nth-child(2) {
    margin-left: 10px;
}

@media (max-width: 600px) {
    .rightSection div:first-child {
        display: none;
    }
    .Grow div:nth-child(2) {
        display: block;
    }
}

@media (max-width: 880px) {
    .LogedInHeader {
        margin-top: 56px;
    }
}