.checkAccount {
    margin: 50px 0px;
}
.but {
    display: flex;
    justify-content: flex-end;
}
.link {
    text-align: center;
    color: #000;
    letter-spacing: .7px;
    box-sizing: border-box;
    padding: 20px;
  cursor: pointer;
    width: 100%;
    background-color:#F8F8F8;
    border: none;
    font-size: 24px;
}
.link:hover {
    background-color:#D21404;
    color: #fff;
}
.link span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .link span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .link:hover span {
    padding-right: 25px;
  }
  
  .link:hover span:after {
    opacity: 1;
    right: 0;
  }
  .probation {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content:flex-start;
    color: #900603;
}
.probation img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
    border: none;
}
.FieldStudy,
.Major {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin-bottom: 20px;
}

.FieldStudy h1,
.Major h1 {
    text-transform: capitalize;
    color: #58595B;
    margin-bottom: 10px;
}

.StudyYears,
.RequiredDocuments,
.ContractSheet,
.Courses {
    width: 100%;
    margin-bottom: 20px;
}

.SubmitApp {
    width: 100%;
    margin: 50px 0px;
}