.Application {
    margin: 50px 0px;
}

.FieldStudy,
.Major {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin-bottom: 20px;
}

.FieldStudy h1,
.Major h1 {
    text-transform: capitalize;
    color: #58595B;
    margin-bottom: 10px;
}

.StudyYears,
.RequiredDocuments,
.ContractSheet,
.Courses {
    width: 100%;
    margin-bottom: 20px;
}

.SubmitApp {
    width: 100%;
    margin: 50px 0px;
}


.textBoxApplication{
    width: 500px;
    height: 200px;
    background-color: #900603;
    color: white;
    align-items: center;
    display: flex;
    justify-content: center;
    text-align: center;
    border-radius: 30px;
}