.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.title {
    color: gray;
    text-transform: uppercase;
    width: 90%;
    align-items: center;
    text-align: left;
    margin-bottom: 3px;
    font-size: 22px;
    font-weight: bold;
}
.text {
    /* border: 1px solid gray; */
    background-color: #fff;
    color: black;
    width: 90%;
    align-items: center;
    text-align: left;
    margin-bottom: 3px;
    font-size: 18px;
    font-weight: bold;
    box-sizing: content-box;
    word-wrap: break-word;
}