.switch {
  position: relative;
  margin: auto;
  width: 100%;
  height: 37px;
  color: #fff;
  font-size: 25px;
  background-color: gray;
}

.quality {
  position: relative;
  display: inline-block;
  width: 50%;
  height: 100%;
}
.quality label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  font-weight: bold;
  text-align: center;
  font-size: 26px;
  transition: transform 0.4s, color 0.4s, background-color 0.4s;
}
.quality input[type="radio"] {
  appearance: none;
  width: 0;
  height: 0;
  opacity: 0;
}
.quality input[type="radio"]:focus {
  outline: 0;
  outline-offset: 0;
}
.quality input[type="radio"]:checked ~ label {
  background-color: #900603;
  color: #fff;
}
.quality input[type="radio"]:active ~ label {
  transform: scale(1.05);
}
