.container {
    background-image: url('../../../assets/images/payment\ backround-01.png');
    background-color:#F8F8F8;
    background-repeat: no-repeat;
    padding:10px 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* align-items: center; */
    margin-left: auto;
    margin-right: auto;
    width: 85%;
    box-sizing: content-box;
}

.textBoxApplication{
    width: 500px;
    height: 200px;
    background-color: #900603;
    color: white;
    align-items: center;
    display: flex;
    justify-content: center;
    text-align: center;
    border-radius: 30px;
}

.paymentInfo{
    width: 80%;
    height: auto;
    margin-top: 5%;
    margin-left: 10%;
}

.CardImage{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}