.greyBoxWithTitle{
    background-color: #c4c5c6;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color:#A91B0D;
    width: 100%;
    height: 50px;
    box-sizing: content-box;
    
    -webkit-box-shadow: 1px 1px 0px #999, 2px 2px 0px #58595B, 3px 3px 0px #58595B, 4px 4px 0px #58595B, 5px 5px 0px #58595B, 6px 6px 0px #58595B;
    ;
    -moz-box-shadow: 1px 1px 0px #999, 2px 2px 0px #58595B, 3px 3px 0px #58595B, 4px 4px 0px #58595B, 5px 5px 0px #58595B, 6px 6px 0px #58595B;
    ;
    box-shadow: 1px 1px 0px #999, 2px 2px 0px #58595B, 3px 3px 0px #58595B, 4px 4px 0px #58595B, 5px 5px 0px #58595B, 6px 6px 0px #58595B;

}

.greyBoxTitle{
    margin-left: 10%;
}