.CoursesTable {
    width: 100%;
    height: 300px;
    border: 2px solid #58595B;
    overflow-x: hidden;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    overflow-y: auto;
    overflow-x: auto;
}
.t {
    color: #000;
}

.CoursesTable div {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Title {
    border-bottom: 2px solid #58595B;
    margin-bottom: 10px;
}

.Title div {
    font-size: 24px;
    font-weight: 900;
}

.CoursesTable::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.9);
    /* border-radius: 10px; */
    background-color: #CCCCCC;
}

.CoursesTable::-webkit-scrollbar {
    width: 12px;
    background-color: #F5F5F5;
}

.CoursesTable::-webkit-scrollbar-thumb {
    /* border-radius: 10px; */
    background-color: #900603;
    background-image: -webkit-linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.4) 50%, transparent, transparent)
}