.Master {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
}

.MajorsBox {
    width: 35%;
}

.Space {
    width: 5%;
}

.Prerequisites {
    width: 60%;
}

@media (max-width: 700px) {
    .Master {
        flex-direction: column;
    }
    .MajorsBox {
        width: 100%;
    }
    .Prerequisites {
        width: 100%;
    }
    .Space {
        width: 100%;
        padding: 10px;
    }
}