.InputDate {
    background: url('../../assets/images/FormDate.png') 97% 50% no-repeat;
    background-size: 20px;
}

.InputDate::-webkit-inner-spin-button {
    display: none;
}

.InputDate::-webkit-calendar-picker-indicator {
    opacity: 0;
}

input[type='date'] {
    padding: 10.2px;
}

.InputDate {
    border: 1px solid black;
    border-radius: 0px;
    background-color: inherit;
    padding: 11.1px;
    width: 100%;
    outline: none;
    cursor: text;
}

.InputDate:focus {
    padding: 9.2px;
}

.InputDate::placeholder,
.NumberInput::placeholder {
    font-size: medium;
    letter-spacing: .5px;
}

.Select {
    border: 1px solid black;
    border-radius: 0px;
    background-color: #F8F8F8;
    padding: 11.1px;
    width: 100%;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: url('../../assets/images/DropDown.png') 100% 50% no-repeat;
    background-size: 40px 110%;
}

.NumberInput {
    border: 1px solid black;
    border-radius: 0px;
    background-color: #F8F8F8;
    padding: 11.1px;
    width: 100%;
    outline: none;
}


/* Chrome, Safari, Edge, Opera */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}


/* Firefox */

input[type=number] {
    -moz-appearance: textfield;
}

.LabelRadioInput {
    user-select: none;
}

.RadioInput {
    display: none;
}

.LabelRadioInput {
    color: black;
    background-color: #F8F8F8;
    border: 1px solid black;
    cursor: pointer;
    transition: all 200ms ease;
}

input[type='radio']:checked+label {
    color: white;
    background-color: #900603;
}

input[type="radio"]+label {
    padding: 9px 20px;
}

.RadioInputContainer {
    margin: 10px 8px;
}

.Upload_File_Container {
    margin: 10px 8px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    width: 96%;
    border: 1px solid black;
    border-right: 0;
}

.Upload_File_Text {
    color: #a9a9a9;
    padding-left: 15px;
}

.Upload_File_Button {
    padding: 0 20px;
    color: white;
    background-color: #900603;
    height: 105%;
    line-height: 40px;
}