.Login {
    width: 100%;
    overflow: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    padding-top: 64px;
}

.LoginContainer {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 100%;
}

.LoginContainerForm {
    width: 60%;
    padding: 20px 0px;
}

.LoginContainerForm form {
    width: 80%;
    margin: auto;
}

.textBoxApplication{
    width: 500px;
    height: 200px;
    background-color: #900603;
    color: white;
    align-items: center;
    display: flex;
    justify-content: center;
    text-align: center;
    border-radius: 30px;
}

.LoginContainerForm  h1 {
    text-align: center;
    color: #A91B0D;
}

.ForgetPassword,
.SignUp {
    text-align: center;
    margin-top: 10px;
}

.ForgetPassword a,
.SignUp a {
    text-decoration: underline;
    font-weight: 500;
    color: black;
}

.SubmitBtn {
    width: 100%;
    padding: 14px 0px;
    text-align: center;
    color: #fff;
    background-color: #900603;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 20px;
    font-size: 16px;
}

.SubmitBtn:hover {
    opacity: .8;
}

@media (max-width: 1000px) {
    .LoginContainer {
        width: 100%;
    }
    .LoginContainer {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .LoginContainerForm {
        width: 96%;
    }
    .LoginContainerForm form h1 {
        text-align: center;
        margin-top: 10px;
    }
    .LoginContainerImage {
        padding: 0px 50px 50px 50px;
        width: 60%;
    }
}

@media (max-width: 599px) {
    .Login {
        padding-top: 48px;
    }
}

@media (min-width: 1900px) {
    .Login {
        padding: 20vh 0;
    }
}

@media (min-width: 2500px) {
    .Login {
        padding: 30vh 0;
    }
}

@media (min-width: 3000px) {
    .Login {
        padding: 35vh 0;
    }
}