.BoxInfo {
    width: 100%;
    height: 150px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid#58595B;
    background-color: #ccc;
    color: #58595B;
}

.BoxInfo div:nth-child(1),
.BoxInfo div:nth-child(2),
.BoxInfo div:nth-child(3) {
    text-align: center;
    margin: 5px;
    font-size: 12px;
    width: 100%;
    text-transform: uppercase;
}

.BoxInfo div:nth-child(2) {
    color: #900603;
    font-weight: 900;
    font-size: 20px;
}