.CoursesToDrop {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.Body {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

.CoursesBox {
    width: 100%;
}

.Space {
    width: 5%;
}

.CoursesToDropName {
    width: 100%;
}

@media (max-width: 700px) {
    .Body {
        flex-direction: column;
    }
    .CoursesBox {
        width: 100%;
    }
    .CoursesToDropName {
        width: 100%;
    }
    .Space {
        width: 100%;
        padding: 10px;
    }
}