.container {
    padding: 10px 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 70%;
}
.cred {
    background-color:white;
    color: black;
    border:1px solid black;
    margin-right: 5px;
    width: 100%;
    align-items: center;
    text-align: center;
    font-weight: bold;
    font-size: 18px;
}
.credVal {
    font-weight: bold;
    font-size: 18px;
    background-color:white;
    color: red;
    border:1px solid black;
    width: 20%;
    align-items: center;
    text-align: center;
}