.payInformation{
    margin-top: 5%;
    width: 100%;
    display: flex;
    flex-direction: column;
    
}

.payInformationLine{
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: 2%;
}

.payInformationItemRight{
    width: 45%;
    margin-right: 5%;
}

.payInformationItemLeft{
    width: 45%;
    margin-left: 5%;
}

.payInformationItemLeftCVV{
    width: 20%;
    margin-left: 30%;
}

.payAmount{
    background-color: #c4c5c6; 
    margin-left: 35%;
    width: 15%;
    display: flex;
    height: 55px;
    flex-direction: column;
    justify-content: center;
}

.payAmountText{
    color: #900603;
    text-align: center;
    font-size: 100%;
}

.payAmountLabel{
    width: 10%;
}
.payButton{
    margin-left: 20.7%;
    width: 23%;

}

.btnPay{
    width: 100%;
    height: 55px;
    text-align: center;
    color: #fff;
    background-color: #900603;
    border: none;
    outline: none;
    cursor: pointer;
    /* background-color: #900603;
    border: none;
    font-size: 150%;
    color: white; */
    /* margin-top: 5px; */
}

.btnPay:hover {
    opacity: .8;
}

.payInput{
    width: 100%;
    height: 55px;
    margin-left: 2px;
}