.BoxWithTitle {
    width: 100%;
    background-color: #f8f8f8;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
}

.TitleSection {
    padding: 20px;
    text-align: center;
    background: linear-gradient(to right, #900603, #E3242B, #BC544B, #A91B0D);
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    font-size: 26px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    color: #fff;
    font-weight: 600;
}

.BodySection {
    padding: 50px 40px;
}