.StudyYear {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.Body {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0px;
}

.Body div {
    width: 30%;
}

.Body.Hide {
    display: none;
}

@media (max-width: 700px) {
    .Body {
        flex-direction: column;
    }
    .Body div {
        width: 100%;
        margin-bottom: 10px;
    }
}