.container {
    background-color: #F8F8F8;
    padding: 10px 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    width: 85%;
    box-sizing: content-box;
}
.text {
    width: 100%;
}
.cont1 {
    width: 60%;
}
.cont1 h2 {
    font-size: 30px;
}
.cont2 {
    color: #900603;
    font-weight: 700;
    height: 200px;
    align-items: center;
    box-sizing: content-box;
    display: flex;
    width: 100%;
}
.cont2 p {
    font-size: 60px;
}
.number {
    font-size: 120px;
}
.im {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}
.probation {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content:flex-start;
    color: #900603;
}
.probation img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
    border: none;
}