.subContainer {
    background-color: #fff;
    padding: 10px 25px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    width: 95%;
    box-sizing: content-box;
}
.container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    box-sizing: content-box;
}