.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    box-sizing: content-box;
}
.text {
    border: 1px solid gray;
    color: black;
    width: 61%;
    align-items: center;
    text-align: left;
    margin-bottom: 3px;
    font-size: 18px;
    font-weight: bold;
    box-sizing: content-box;
    word-wrap: break-word;
}
.subContainer {
    padding: 1% 2%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    box-sizing: content-box;
}