.InputDocument {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 10px;
}

.InputDocument div:first-child {
    text-transform: uppercase;
}

.InputDocument div:nth-child(2) input::-webkit-file-upload-button {
    background-color: #900603;
    color: #fff;
    border: none;
    outline: none;
    padding: 5px;
}

@media (max-width: 600px) {
    .InputDocument {
        flex-direction: column;
        align-items: center;
    }
    .InputDocument div:first-child {
        margin-bottom: 10px;
    }
    .InputDocument div:nth-child(2) input::-webkit-file-upload-button {
        background: none;
    }
    .InputDocument div:nth-child(2) input {
        background: #900603;
        color: #fff;
    }
}