.Prerequisites {
    width: 100%;
    height: 300px;
    overflow-y: auto;
    overflow-x: hidden;
}

.Prerequisites h2 {
    text-align: center;
    color: #900603;
    margin-bottom: 10px;
}

.Prerequisites div {
    color: #58595B;
    text-align: justify;
    padding-right: 30px;
    letter-spacing: 1px;
    line-height: 1.6;
}

.Prerequisites::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.9);
    /* border-radius: 10px; */
    background-color: #CCCCCC;
}

.Prerequisites::-webkit-scrollbar {
    width: 12px;
    background-color: #F5F5F5;
}

.Prerequisites::-webkit-scrollbar-thumb {
    /* border-radius: 10px; */
    background-color: #900603;
    background-image: -webkit-linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.4) 50%, transparent, transparent)
}