.ReceiptRow {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0px;
}

.acceptButton{
    width: 80%;
    height: 40px;
    background-color: #900603;
    color: #fff;
}

.acceptButton:hover {
    opacity: .8;
}

.textBoxApplication{
    width: 150px;
    height: 200px;
    background-color: white;
    color: black;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    border-radius: 30px;
}

.popupDisplay{
    display: flex;
    flex-direction: column;
}

.selectYearSemester{
    width: 50%;
}