.container {
    padding: 10px 30px;
    display: flex;
    flex-direction: column;
    justify-content:space-around;
    align-items: center;
    box-sizing: content-box;
    width: 30%;
    font-weight: bold;
    font-size: 15px;
}
.cont {
  display: flex;
  flex-direction:row;
  justify-content:space-between;
  align-items: center;
  width: 100%;
}
.attribute {
  text-transform: uppercase;
  color:#900603;
  width: 75px;
  font-size: 16px;
}
.value {
  width: 70px;
  text-align: center;
}