.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.gpa {
    background-color: white;
    border: 1px solid gray;
    color: #900603;
    text-transform: uppercase;
    width: 60%;
    align-items: center;
    text-align: center;
    margin-bottom: 3px;
    font-size: 26px;
    font-weight: bold;
}
.gpaValue {
    background-color: #900603;
    border: 1px solid #900603;
    color: white;
    width: 60%;
    align-items: center;
    text-align: center;
    font-size: 26px;
    font-weight: bold;
}