.footer-container {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.footer-inner-container,
.footer-inner1-container {
    padding: 30px;
}

.footer-inner-container {
    width: 100%;
    background-color: #900603;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.footer-inner-container>div {
    width: 25%;
}

.footer-inner-container div span {
    color: white;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.footer-inner-container div p {
    color: white;
    letter-spacing: 1px;
}

.partLinks {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.partLinks a {
    padding: 10px;
    text-decoration: none;
    color: white;
    /* width: 100%; */
}

.partLinks a:hover {
    text-decoration: underline;
}

.call-us {
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.call-us div {
    width: 100%;
}

.line {
    width: 100%;
    height: 1px;
    background: #fff;
}

.social-media-icons {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer-links {
    color: white;
    text-decoration: none;
    word-spacing: .5px;
    cursor: pointer;
}

.footer-rounded {
    background-color: inherit;
    padding: 5px;
    width: 30px;
    height: 30px;
    text-align: center;
    border-radius: 50%;
    border: 2px solid #fff;
}

.footer-rounded:hover {
    border: 2px solid #900603;
}

.footer-inner1-container {
    width: 100%;
    padding: 30px 10px;
    background: rgb(203, 203, 203);
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: rgb(166, 29, 49);
    font-weight: 700;
}

.privacy-usage-copyright {
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.privacy-usage-copyright>div {
    border-right: 1px solid rgb(166, 29, 49);
    padding: 0px 22px;
}

.privacy-usage-copyright>div:last-child {
    border-right: none;
}

.privacy-usage-copyright-links {
    text-decoration: none;
    color: inherit;
}

@media (max-width: 960px) {
    .footer-inner-container {
        flex-direction: column;
        align-items: center;
        text-align: center;
        letter-spacing: 1px;
    }
    .footer-inner-container div {
        width: 100%;
        margin: 10px 0px;
    }
    .partLinks {
        flex-direction: row;
        flex-wrap: wrap;
    }
    .footer-inner1-container {
        flex-direction: column;
        text-align: center;
        justify-content: flex-start;
        align-items: center;
        font-size: 12px;
    }
    .privacy-usage-copyright {
        margin-top: 10px;
        font-size: 12px;
    }
    .CallUsTitle {
        width: 100%;
        text-align: center;
        text-transform: none;
    }
}