.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
}
.popupInner {
  position: relative;
  padding: 32px;
  width: 100%;
  max-width: 900px;
  background-color: #fff;
}
.popupInner .btn {
  position: relative;
  top: 16px;
  right: 16px;
}
.popupInner .btn {
  position: absolute;
  top: 20px;
  right: 30px;
  transition: all 200ms;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
  background: none;
  border: none;
  cursor: pointer;
}
.popupInner .btn:hover {
  color: #900603;
}
