.SubscribeSemesterContent {
    align-items: center;
    justify-content: center;
    margin: 50px 0px;
    

    
}

.textBoxApplication{
    width: 500px;
    height: 200px;
    background-color: #900603;
    color: white;
    align-items: center;
    display: flex;
    justify-content: center;
    text-align: center;
    border-radius: 30px;
}
.but {
    display: flex;
    justify-content: flex-end;
}
.divSpace{
    margin-top: 100px;
}
.FieldStudy,
.Major {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin-bottom: 20px;
}

.FieldStudy h1,
.Major h1 {
    text-transform: capitalize;
    color: #58595B;
    margin-bottom: 10px;
}

.StudyYears,
.RequiredDocuments,
.ContractSheet,
.Courses {
    width: 100%;
    margin-bottom: 20px;
}

.SubmitApp {
    width: 100%;
    margin: 50px 0px;
}

.acceptButton{
    width: 100%;
    height: 40px;
    background-color: #900603;
    color: #fff;
}

.acceptButton:hover {
    opacity: .8;
}

.popupDisplay{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.selectYearSemester{
    width: 25%;
}