.container {
    padding: 10px 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}
.label {
    background-color:#58595B;
    color: #fff;
    border:1px solid #58595B;
    margin-right: 5px;
    width: 40%;
    align-items: center;
    text-align: center;
    font-weight: bold;
    font-size: 22px;
    padding: 5px 0;
}
.value {
    font-weight: bold;
    font-size: 22px;
    background-color:white;
    color: #58595B;
    border:1px solid #58595B;
    width: 60%;
    align-items: center;
    text-align: center;
    padding: 5px 0;
}