.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.prob {
    text-transform: uppercase;
    background-color: white;
    border: 1px solid gray;
    color: #900603;
    width: 80%;
    align-items: center;
    text-align: center;
    margin-bottom: 3px;
    font-size: 26px;
    font-weight: bold;
    box-sizing: content-box;
    text-wrap: break-word;
}
.choice {
    width: 80%;
}