.historyFormRoot{
    margin-top: 5%;
}

.historyForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: auto;
    height: auto;
    padding: 5%;
    margin-top: 5%;
    margin-right: 5%;
    margin-left: 5%;
}

.historyFormLine{
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-bottom: 2%;
}

.historyFormLineLeft{
    width: 45%;
    margin-right: 5%;
}

.historyFormLineRight{
    width: 45%;
    margin-left: 5%;
}

.historyDarkGreyBox{
    background-color: #58595B;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color:white;
    width: 100%;
    height: 50px;
    box-sizing: content-box;
}

.historyDarkGreyBoxText{
    text-align: center;
}

.greyText{
    color:#58595B;
    margin-right: 5%;
}

.rubyText{
    color:#900603;
    margin-left: 5%;

}