.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.cost {
    color: #900603;
    text-transform: uppercase;
    width: 150px;
    align-items: center;
    text-align: left;
    margin-bottom: 3px;
    font-size: 22px;
    font-weight: bold;
}
.costValue {
    background-color: #900603;
    border: 1px solid #900603;
    color: white;
    width: 150px;
    align-items: center;
    text-align: center;
    font-size: 22px;
    font-weight: bold;
}